.feature--grid--hexagon {
	align-items: center;
	border-left: 4px solid #08d4d4;
	border-radius: 5px;
	border-right: 4px solid #08d4d4;
	display: flex;
	font-size: 3rem;
	height: 3.6rem;
	justify-content: center;
	margin: 2rem auto;
	position: relative;
	text-align: center;
	width: 6.1rem;
	z-index: 1
}

.feature--grid--hexagon:after,
.feature--grid--hexagon:before {
	background: inherit;
	content: "";
	height: 3.6rem;
	left: 5;
	overflow: hidden;
	position: absolute;
	top: 0;
	width: 5.7rem;
	z-index: -1
}

.feature--grid--hexagon:before {
	-webkit-transform: rotate(-60deg);
	transform: rotate(-60deg)
}

.feature--grid--hexagon:after,
.feature--grid--hexagon:before {
	border-left: 4px solid #08d4d4;
	border-radius: 5px;
	border-right: 4px solid #08d4d4
}

.feature--grid--hexagon:after {
	-webkit-transform: rotate(60deg);
	transform: rotate(60deg)
}

